<template>
  <div class="secure">
    <div class="nav-bg"></div>
    <van-nav-bar title="安全中心" left-text="返回" left-arrow :fixed="true" @click-left="back" />

    <div class="secure-box">
      <div>
        <van-cell title="修改密码" isLink @click="openPasswordPopup" />
        <van-cell title="注销账号" isLink @click="destroyAccount" />
      </div>
    </div>
    <div class="btn-box">
      <van-button round block color="#0C34BA" @click="logout">退出登录</van-button>
    </div>

    <van-popup
      class="popup-box"
      closeable
      v-model="isOpen"
      position="bottom"
      :style="{ height: '60%' }"
    >
      <div class="popup-item">
        <div class="password-text">修改密码</div>
        <van-form>
          <van-field
            v-for="(item, index) in passwords"
            :key="index"
            :type="item.isPassword ? 'password' : 'text'"
            :label="item.title"
            placeholder="6-16位密码且以字母开头"
            v-model="item.password"
            :right-icon="item.isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewPassword(item)"
          />
          <div class="login-btn">
            <van-button
              round
              block
              color="#0C34BA"
              native-type="submit"
              @click="confirmPassword"
            >确认修改</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/secure.less";
import { utils } from "../assets/js/utils";
import { validForm } from "../assets/js/validForm";

export default {
  data() {
    return {
      isOpen: false,
      passwords: {
        old: {
          password: "",
          isPassword: true,
          title: "旧密码",
        },
        new: {
          password: "",
          isPassword: true,
          title: "新密码",
        },
      },
    };
  },
  methods: {
    //返回上一页
    back() {
      this.$router.go(-1);
    },
    // 查看密码
    viewPassword(item) {
      item.isPassword = !item.isPassword;
    },
    // 退出登录
    logout() {
      this.$dialog
        .confirm({
          title: "退出登录",
          message: "真的不再挽留下我了吗 T_T !",
          confirmButtonColor: "#0c34BA",
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          localStorage.removeItem("CSTK");

          setTimeout(() => {
            this.$toast.clear();
            this.$router.push({ name: "Login" });
          }, 600);
        });
    },
    //修改密码
    openPasswordPopup() {
      this.isOpen = true;
    },
    //确认修改密码
    confirmPassword() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let o = {
        oldPassword: {
          value: this.passwords.old.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "旧密码支持字母数字_组合",
        },
        password: {
          value: this.passwords.new.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "新密码支持字母数字_组合",
        },
      };

      //验证表单
      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }

      // 发起修改密码请求
      // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      // 序列参数化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oldPassword: this.passwords.old.password,
        password: this.passwords.new.password,
      });

      this.axios({
        method: "POST",
        url: "/updatePassword",
        data,
      })
        .then((result) => {
          setTimeout(() => {
            this.$toast.clear();
            this.$router.push({ name: "Login" });
          }, 600);
          
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    // 注销当前账号
    destroyAccount() {
      this.$dialog
        .confirm({
          title: "注销账号",
          message: "是否注销账号,一经注销将无法恢复!",
          confirmButtonColor: "#0c34BA",
        })
        .then((result) => {
          

          //获取token字符集
          let tokenString = localStorage.getItem("CSTK");
          if (!tokenString) {
            return this.$router.push({ name: "Login" });
          }

          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          // 参数序列化
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
          });

          this.axios({
            method: "POST",
            url: "/destroyAccount",
            data,
          })
            .then((result) => {
              this.$toast.clear();

              this.$toast(result.data.msg);
              if (result.data.code == "G001") {
                localStorage.removeItem("CSTK");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 600);
              }
            })
            .catch((err) => {
              
              this.$toast.clear();
            });
        })
        .catch((err) => {
          
        });
    },
  },
  created() {},
};
</script>